<template>
	<transition enter-active-class="animated bounceInLeft" leave-active-class="animated slideOutRight">
		<div class="detail"v-show="detailshow">
			<p class="title">{{seller.name}}</p>
			<div class="stars">
				<star :size="'big'":score = 'seller.score'></star>
			</div>
			<div class="line">
				<div class="border"></div>
				<div class="msgtitle">优惠信息</div>
				<div class="border"></div>
			</div>
			<ul v-if="seller.supports" class="supports">
				<li class="support-item" v-for="item in seller.supports">
					<span class="icon" :class="iconClassMap[item.type]"></span>
					<span class="txt">{{item.description}}</span>
				</li>
			</ul>
			<div class="line">
				<div class="border"></div>
				<div class="msgtitle">商家公告</div>
				<div class="border"></div>
			</div>
			<div class="notice">
				{{seller.bulletin}}
			</div>
			<div class="close">
				<i class="el-icon-close closebtn"@click="close"></i>
			</div>
		</div>
	</transition>
</template>	
<script>
	import {mapGetters} from 'vuex'
	import Star from '../star/star.vue'

	export default {
		components:{
			'star':Star
		},
		computed : {
			...mapGetters([
				'detailshow',
				'seller'
			])
		},
		created(){
			this.iconClassMap = ["decrease","discount","special","invoice","guarantee"]
		},
		methods : {
			close(){
				this.$store.dispatch('closedetail');
			}

		}
	}
</script>	
<style lang="less" scoped>
	.detail{
		position: fixed;
		z-index:999;
		top:0;
		left:0;
		right:0;
		bottom: 0;
		background: rgba(7,17,27,0.8);		
		.title{
			color: rgb(255,255,255);
			font-size: 0.32rem;
			font-weight: 700;
			line-height: 0.32rem;
			text-align: center;
			padding: 1.28rem 0 0.35rem 0;
		}
		.stars{
			display: flex;
			justify-content:center;
			.star{
				height: 0.48rem;
				padding-right: 0.4rem;
			}
		}
		.line{
			padding:0.56rem 0.72rem 0.24rem 0.72rem;
			display: flex;
			align-items:center;
			.border{
				height: 0.03rem;
				width: 2.24rem;
				background: rgba(255,255,255,0.2);
			}
			.msgtitle{
				font-size: 0.28rem;
				font-weight: 700;
				line-height: 0.28rem;
				color:rgb(255,255,255);
				padding: 0 0.23rem;
			}
		}
		.supports{
			padding-left: 0.96rem;
			.support-item{
			margin-top: 0.24rem;				
				.icon{
					display: inline-block;
					vertical-align: top;
					width: 0.32rem;
					height: 0.32rem;
					padding-right: 0.12rem;
					background-size:0.32rem 0.32rem;
					vertical-align: middle;
					&.decrease{
						background-image:url("../imgs/decrease.png");
						background-repeat: no-repeat;
					}
					&.discount{
						background-image:url("../imgs/discount.png");
						background-repeat: no-repeat;
					}
					&.special{
						background-image:url("../imgs/special.png");
						background-repeat: no-repeat;
					}
					&.invoice{
						background-image:url("../imgs/invoice.png");
						background-repeat: no-repeat;
					}
					&.guarantee{
						background-image:url("../imgs/guarantee.png");
						background-repeat: no-repeat;
					}
				}
				.txt{
					font-size: 0.24rem;
					font-weight: 200;
					color:rgb(255, 255, 255);
					line-height: 0.24rem;
				}
			}
		}
		.notice{
			font-size: 0.24rem;
			font-weight: 200;
			line-height: 0.48rem;
			color:rgb(255,255,255);
			padding:0.48rem 0.96rem 0 0.96rem;
		}
		.close{
			position: absolute;
			bottom: 0.64rem;
			left: 47.5%;
			.closebtn{
				font-size: 0.5rem;
				color: rgba(255, 255, 255, 0.2);
			}

		}
	}
</style>	


