<template>
	<div class="header">
		<div class="background">
			<img :src="seller.avatar">
		</div>
		<img :src="seller.avatar" class="avatar" >
		<div class="right">	
			<img src="../imgs/brand.png" class="brand">
			<span class="title">{{seller.name}}</span>
			<p class="bird">{{seller.description}} / {{seller.deliveryTime}}分钟送达</p>
			<img src="../imgs/decrease.png"class="decrease">
			<span v-if="seller.supports" class="decreaseText">{{seller.supports[0].description}}</span>
		</div>
		<div class="count"@click='show'>
			5个 <i class="el-icon-arrow-right"></i>
		</div>
		<div class="notice">
			<img src="../imgs/bulletin.png">
			<span>{{seller.bulletin}}</span>
		</div>
		<Detail></Detail>	
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import Detail from '../detail/detail.vue'

	export default {
		computed:{
			...mapGetters([
				'seller',
				])
		},
		components:{
			Detail
		},
		created(){
			this.$store.dispatch('getSeller');
		},
		methods:{
			show(){
				this.$store.dispatch('showdetail');
			}
		}
	}
</script>
<style lang="less" scoped>
	.header{
		height:2.68rem;
		position: relative;
		overflow: hidden;
		background: rgba(7,17,27,0.5);
		width: 100%;
		.background{
			position: absolute;
			top:0;
			bottom: 0;
			left:0;
			right:0;
			z-index:-5;
			filter:blur(0.1rem);
			img{
				width: 100%;
				height: 100%;
			}
		}
		.avatar{
			width:1.28rem;
			height: 1.28rem;
			border-radius: 0.04rem;
			padding-top:0.48rem;
			padding-left: 0.48rem;
			padding-bottom: 0.36rem;
		}
		.right{
			position: absolute;
			top:0.52rem;
			left:2.08rem;
			.title{
				font-size: 0.32rem;
				color:rgb(255,255,255);
				font-weight: bold;
				line-height: 0.36rem;
				float: right;
				padding-left: 0.12rem;
			}
			.brand{
				height: 0.36rem;
			}
			.bird{
				margin-top:0.16rem;
				font-size: 0.24rem;
				color:rgb(255,255,255);
				font-weight: 200;
				line-height: 0.24rem;
			}
			.decrease{
				padding-top: 0.2rem;
				height: 0.24rem;
				vertical-align: bottom;
			}
			.decreaseText{
				padding-left: 0.08rem;
				font-size: 0.2rem;
				line-height: 0.22rem;
				color:rgb(255,255,255);
			}
		}
		.count{
			background-color: rgba(0,0,0,0.2);
			border-radius: 0.4rem;
			height: 0.48rem;
			width: 1rem;
			float: right;
			margin-right: 0.24rem;
			text-align: center;
			line-height: 0.48rem;
			position: absolute;
			right:0.24rem;
			bottom: 0.8rem;
			color:rgb(255,255,255);
		}
		.notice{
			height: 0.5rem;
			background-color: rgba(7,17,27,0.2);
			display: flex;
			justify-content: center;
            align-items: center;
			img{
				height:0.3rem;
				padding:0 0.08rem 0 0.24rem; 
			}
			span{
				overflow: hidden;
				white-space: nowrap;
				text-overflow:ellipsis;
				width:6.8rem;
				color:rgb(255,255,255);

			}
		}
	}
</style>


