<template>
    <ul class="calcute">
        <transition name="move">        
            <li @click.stop="minus" v-show="food.count"><i class="iconfont icon-jianhao"></i></li>
        </transition>
        <li v-show="food.count">{{food.count}}</li> 
        <li @click.stop="add" ><i class="iconfont icon-jiahao"></i></li>
    </ul>
</template>

<script>
    import Vue from 'vue'
    import {mapGetters} from "vuex"

    export default{
        props:["food"],
        methods:{
            add(){
                if(!this.food.count){
                    Vue.set(this.food,"count",1);
                }else{
                    this.food.count++;
                }

            },
            minus(){
                if(this.food.count){
                  this.food.count--;  
                }
            }
        }
    }

</script>

<style lang="less" scoped>
    .calcute{
        /*加减动画  */
        .move-enter-active, .move-leave-active{
            transition: all 0.5s;
        }
        .move-leave-active {
            transition: all 0.5s;
            transform:translate(30px) rotate(90deg);
        }
        .move-enter, .move-leave-active {
            transform:translate(20px) rotate(90deg);
            opacity: 0;
        }
        display: flex;
        li{
            .icon-jianhao{
                font-size:0.48rem;
                color:rgb(0,160,220);
                line-height:0.48rem;
            }
            .icon-jiahao{
                font-size:0.48rem;
                color:rgb(0,160,220);
                line-height:0.48rem;
            }
        }
        li:nth-of-type(2){
            display:inline-block;
            text-align:center;
            width:0.48rem;
            font-size:0.2rem;
            color:rgb(147,153,159);
            line-height:0.48rem;
        }

    } 
</style>



