<template>
    <div class="seller" ref="menuWrapper">
        <div>
            <div class="title">
                <div class="tt">
                    <div class="titleL">
                        <span>{{seller.name}}</span>
                        <div class="score">
                            <Star :size="'middle'" :score='seller.score'></Star>
                            <span>({{seller.ratingCount}})</span>
                            <span>月售{{seller.sellCount}}单</span>
                        </div>
                    </div>
                    <div class="titleR" @click="show">
                        <i class="iconfont icon-xin1" ref="Xin"></i>
                        <p ref="collection">收藏</p>
                    </div>
                </div>
                <div class="line1"></div>
            </div>
            <div class="info">
                <div>
                    <ul>
                        <li>起送价</li>
                        <li><span>{{seller.minPrice}}</span>元</li>
                    </ul>
                </div>
                <div>
                    <ul class="second">
                        <li>商家配送</li>
                        <li><span>{{seller.deliveryPrice}}</span>元</li>
                    </ul>
                </div>
                <div>
                    <ul>
                        <li>平均配送时间</li>
                        <li><span>{{seller.deliveryTime}}</span>分钟</li>
                    </ul>
                </div>
            </div>
            <div class="line"></div>
            <div class="active">
                 <p class="activetitle">公告与活动</p>
                 <div class="actcontent">{{seller.bulletin}}</div>
                 <div class = "actlogo">
                    <ul>
                        <li  v-for="(v,k) in seller.supports" :key="k">
                            <div class="act">
                                <img  :src='imgSrc[v.type].src'>
                                <span v-if="seller.supports" >{{v.description}}</span>
                            </div>
                            
                    </li>
                    </ul>
                 </div>
            </div>
            <div class="line"></div>
            <div class="pic">
                 <p class="pictitle">商家实景</p>
                 <div ref="menu2Wrapper" class="imgs">
                    <div>
                        <ul>
                            <li v-for="(v,k) in seller.pics" :key="k">
                                <span><img :src="v" alt=""></span>
                            </li>    
                        </ul>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <div class="more">
                <p>商家信息</p>
                <ul>
                    <li v-for="(v,k) in seller.infos" :key="k">
                        <span>{{v}}</span>
                    </li>    
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Star from '../star/star.vue'
    
    export default{
        data(){
            return {
                showXin:true,
                imgSrc:[
					{type:0,src:'/static/img/decrease.png'},
					{type:1,src:'/static/img/discount.png'},
					{type:2,src:'/static/img/guarantee.png'},
					{type:3,src:'/static/img/invoice.png'},
					{type:4,src:'/static/img/special.png'},
				] ,
            }
        },
         computed:{
            ...mapGetters(['seller']),
        },
        components: {
            Star
        },
        created(){
            this.$store.dispatch('getSeller');
            this.$nextTick(() => {
                setTimeout(()=>{
                    this._initScroll();
                },100)    
            });
        },
         methods:{
            _initScroll(){
                this.menuBS= new BScroll(this.$refs.menuWrapper, {
                    click: true
                });
                this.menuBS= new BScroll(this.$refs.menu2Wrapper, {
                    scrollX: true,
                    click: true
                });
            },
            show(){
                if(this.showXin){
                   this.$refs.Xin.style.color="rgb(240,20,20)";
                   this.$refs.collection.innerHTML="已收藏";
                   this.showXin=false;
                }else{
                   this.$refs.Xin.style.color="rgb(147,153,159)";
                   this.$refs.collection.innerHTML="收藏";
                   this.showXin=true;
                } 
            }

    }

    }
</script>

<style lang="less" scoped>
    .seller{
        height: 9.5rem;
        overflow: hidden;
        .title{
            padding:0.36rem;
            span{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height: 0.28rem;
            }
              .star{
                margin-right:0.16rem;
            }  
            .line1 {
                height: 0.01rem;
                background: rgba(7, 17, 27, 0.1);
            }
            .tt{
                display: flex;
                position: relative;   
                .titleL {
                    .score{
                            display: flex;
                            height:0.36rem;
                            margin-top: 0.16rem;
                            margin-bottom: 0.36rem;
                            span{
                                font-size:0.2rem;
                                color:rgb(77,85,93);
                                line-height: 0.4rem;
                                font-weight: 200;
                        }
                            span:nth-of-type(1){
                                margin-right:0.24rem;
                            }                  
                    }
                }
                .titleR{
                    position:absolute;
                    right:0.36rem;
                    width:0.8rem;
                    text-align: center;
                    i{
                        font-size:0.48rem;
                        color:rgb(147,153,159);
                        line-height:0.48rem;

                    }
                    p{
                        font-size:0.2rem;
                        color:rgb(147,153,159);
                        line-height:0.2rem;
                        margin-top:0.08rem;
                    }
                }
            }

        }
        .info{
            width:100%;
            display:flex;
            div{
                flex:1; 
                text-align:center;
                padding-bottom:0.36rem;
                ul{
                    li:nth-of-type(1){
                        font-size:0.2rem;
                        color:rgb(147,153,159);
                        line-height: 0.2rem;
                        margin-bottom: 0.08rem;
                    }
                    li:nth-of-type(2){
                        font-size:0.2rem;
                        color:rgb(7,17,27);
                        line-height: 0.48rem;
                        font-weight: 200;
                        
                    }
                    span{
                    font-size:0.48rem;  
                    }
                }
                .second{
                    border-left:0.01rem solid rgba(7,17,27,0.1);
                    border-right:0.01rem solid rgba(7,17,27,0.1);
                }

            }
        }
        .line {
            height: 0.32rem;
            width: 100%;
            background: #f3f5f7;
            border-top: 1px solid rgba(7, 17, 27, 0.1);
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        }
        .active{
            padding:0.36rem 0.36rem 0 0.36rem;
            .activetitle{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height: 0.28rem;
            }
            .actcontent{
                margin: 0.16rem 0.24rem 0.32rem 0.24rem;
                font-size:0.24rem;
                font-weight: 200;
                color:rgb(240,20,20);
                line-height: 0.48rem;
            }
            .actlogo{
                li{
                    padding:0.32rem 0;
                    border-top:0.01rem solid rgba(7,17,27,0.1);
                    .act{
                        margin:0 0.24rem;
                        img{
                            width: 0.32rem;
                            height: 0.32rem;
                            margin-right:0.12rem;

                        }
                        span{
                            font-size:0.24rem;
                            font-weight: 200;
                            color:rgb(7,17,27);
                            line-height: 0.32rem;
                            vertical-align: top;
                        }
                    }
                }
            }
        }
        .pic{
            padding:0.36rem 0 0.36rem 0.36rem; 
            .pictitle{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height: 0.28rem;
                margin-bottom: 0.24rem;
            }
            .imgs{
                width: 7.44rem;
                overflow: hidden;
                div{
                    width:10rem;
                    ul{
                        display:flex;
                        li{
                            padding-left:0.12rem;
                            img{
                                width:2.4rem;
                                height:1.8rem;
                            }
                        }
                    }
                }
            }
        }
        .more{
            padding:0.36rem 0.36rem 0 0.36rem;
            margin-bottom: 0.2rem;
            p{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height: 0.28rem;
                margin-bottom: 0.24rem; 
            }
            li{
                    padding:0.32rem 0.24rem;
                    border-top:0.01rem solid rgba(7,17,27,0.1);
                    span{
                        font-size:0.24rem;
                        font-weight:200;
                        color:rgb(7,17,27);
                        line-height:0.32rem;
                    }
                }

        }
        
    }
</style>



