import types from '../types.js'
import axios from 'axios'

const state = {
	goods:[],
	foodshow : false,
	food:{}
}

const getters = {
	goods(state){
		return state.goods;
	},
	food(state){
		return state.food;
	},
	foodshow(state){
		return state.foodshow;
	}
}

const actions = {
	getGoods({commit,state}){
		axios.get('../../../static/data.json').then(resp => {
			if(resp.data.errno === 0){
				commit(types.GET_GOODS,resp.data.goods);
			}
		})
	},
	showfood({commit},f){
		console.log(1);
		commit(types.SHOW_FOOD,f);
	},
	hidefood({commit}){
		commit(types.HIDE_FOOD);
	}
}

const mutations = {
	[types.GET_GOODS](state,data){
		state.goods = data;
	},
	[types.HIDE_FOOD](state){
		state.foodshow = false;
	},
	[types.SHOW_FOOD](state,f){
		console.log(2);
		state.foodshow = true;
		state.food = f;
	}
}

export default {
	state,
	getters,
	actions,
	mutations

}


