<template>
	<transition enter-active-class="animated bounceInLeft" leave-active-class="animated slideOutRight">
		<div class="goodsdetail" ref="menuWrapper" v-if="foodshow">
			<div class="detail-content">
				<div class="image-header">
					<img :src="food.image" alt="">
				</div>
				<div class="cc" @click = 'hidefood'>
					<i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
				</div>
				<div class="info">
					<p class="name">{{food.name}}</p>
					<span class="sellCount">月售{{food.sellCount}}份</span>
					<span>好评率{{food.rating}}%</span>
					<div class="price">
						<span class="newPrice">￥{{food.price}}</span>
						<span class="oldPrice" v-if="food.oldPrice>0">￥{{food.oldPrice}}</span>
						<!-- <el-button class="joincart" @click='joincart' v-if="!food.count">加入购物车</el-button> -->
						<span class="cart" @click='joincart' v-if="!food.count">加入购物车</span>
						<div  class="count">
							<count :food='food' v-show="food.count"></count>   
						</div>
					</div>
				</div>
				<div class="line"></div>
				<div class="desc">
					<p class="title">商家介绍</p>
					<p class="infofood">{{food.info}}</p>
				</div>
				<div class="line"></div>
				<!-- 重复 -->

				<div class="center">
					<div class="btn">
						<p>商品评价</p>
						<el-button class="btn1"@click="getAll":class="{'choosed':'getAll' === cc}">全部 <span>{{food.ratings.length}}</span></el-button>
			            <el-button class="btn2"@click="ok":class="{'ok':'ok' === cc}">满意 <span>{{countnum}}</span></el-button>
			            <el-button class="btn3"@click="no":class="{'no':'no' === cc}">不满意 <span>{{food.ratings.length-countnum}}</span></el-button>
					</div>
					<div class="check">
						<i class="iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
		                <span>只看有内容的评价</span>
					</div>			
				</div>
				<div class="line1"></div>
				<div class="comment">
					<div class="menu-wrapper">
						<ul>
							<li v-for="(v,k) in ratLists":key="k" class="menu-list" v-show="all || yes === v.rateType">
								<div class="rat">
									<div class="userinfo">
										<div class="rateTime">{{v.rateTime | date}}</div>
										<div>{{v.username}}<img :src="v.avatar"></div>
									</div>
									<div class="satisfy">
                                    	<span v-if="v.rateType == 0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                                    	<span v-else="v.rateType == 1" class="bad"><i class="iconfont icon-down"></i></span>
                                    	<span class="text">{{v.text}}</span>
									</div>
								</div>
							</li>
						</ul>
					</div>
				</div>				
			</div>
			<shopCart></shopCart>               
		</div>
	</transition>
	
</template>

<script>
	import {mapGetters} from 'vuex'
	import Vue from 'vue'
	import count from '../goods/count.vue'
	import shopCart from '../shopCart/shopCart.vue' 
	export default {
		data(){
			return{
				all:true,
				yes:-1,
				flag:false,
				cc:'getAll'
			}
		},
		components:{
			count,
			shopCart
		},
		filters:{
            date:function(value){
                function toDouble(n){
                    return n<10?'0'+n:n;
                }
                var d=new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
		computed:{
			...mapGetters([
				'foodshow',
				'food',
			]),
			ratLists(){
				if(this.flag){
					return this.food.ratings.filter((item)=>{
						return item.text
					})
				}
				return this.food.ratings;
			},
			countnum(){
                return  this.food.ratings.filter((item) => {
                        return item.rateType === 0
                    }).length;
            }
		},
		methods:{
			hidefood(){
				this.$store.dispatch('hidefood');
			},
			_initMenu() {
                this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
            },
             changeFlag() {
                this.flag = !this.flag;
                this.refresh()
            },
            refresh() {
                setTimeout(() => {
                    this.menuBS.refresh()
                }, 20)
            },
            ok() {
                this.yes = 0
                this.all = false
                this.refresh()
                this.cc = 'ok';
            },
            no() {
                this.yes = 1
                this.all = false
                this.refresh()
                this.cc = 'no';
            },
            getAll() {
                this.yes = -1
                this.all = true
                this.refresh()
                this.cc = 'getAll'; 
            },
            joincart(){
                if(!this.food.count){
                    Vue.set(this.food,"count",1);
                }
            }
		},
		// watch:{
		// 	food(){
		// 		this.$nextTick(()=>{
		// 			this._initMenu();
		// 		})
		// 	}
		// },
		watch:{
			food(){
				this.$nextTick( () => {
					setTimeout( () => {
			            if( !this.menuBS ){
			            	this.menuBS = new BScroll (this.$refs.menuWrapper, {
			            		click : true
			            	})
			            }else{
			            	this.menuBS.refresh();
			            }
					},200)
				})
			}
		},
		created(){
			this.$store.dispatch('getRatings');
			// this.$nextTick(() => {
   //              setTimeout(() => {
   //                  this._initMenu();
   //              },100)    
   //          });
		}
	}
</script>

<style lang="less" scoped>
	.goodsdetail{
		position:fixed;
		top: 0;
		z-index: 9999;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		 margin-bottom: 1.1rem;
		.detail-content{
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.cc{
				position: fixed;
				top:10px;
				left: 10px;
				width: 10px;
				height: 10px;
				padding:10px;
				color: white;
			}
			.info{
				padding: 0.36rem;
				font-size: 0.2rem;
				color:rgb(147,153,159);
				line-height: 0.2rem;
				.name{
					font-size: 0.28rem;
					font-weight: 700;
					color: rgb(7,17,27);
					line-height: 0.28rem;
					padding-bottom: 0.16rem;
				}
				.sellCount{
					padding-right:0.24rem;
				}
				.price{
					padding-top: 0.36rem;
					font-weight: 700;
					line-height: 0.48rem;
					.newPrice{
						font-size: 0.28rem;					
						color:rgb(240,20,20);
						padding-right: 0.24rem;					
					}
					.oldPrice{
						font-size: 0.2rem;
						color:rgb(147,153,159);
						text-decoration: line-through;
					}
				}
				.cart{
					width: 1.48rem;
					height: 0.46rem;
					border-radius: 0.24rem;
					background-color:rgb(0,160,220);
					line-height: 0.46rem;
					color:#fff;
					text-align: center;
					position: absolute;
					right: 0.36rem;
					top:8.8rem;
				}
				.count{
					position: absolute;
					height: 0.3rem;
					right:0.36rem;
					top:8.8rem;
				}
			}
			.line{
				height: 0.32rem;
				background-color: #f3f5f7;
			}
			.desc{
				padding: 0.36rem;
				.title{
					font-size:0.28rem;
     				color:rgb(7,17,27);
      				line-height:0.28rem;
      				padding-bottom: 0.12rem;
				}
				.infofood{
					padding-left: 0.16rem;
					font-size: 0.24rem;
					font-weight: 200;
					line-height: 0.48rem;
					color:rgb(77,85,93);
				}
			}
			.center{
			padding:0.36rem 0.36rem 0 0.36rem;
			.btn{
				margin-bottom: 0.36rem;
				font-size:0.24rem;
                border-radius:0.02rem;
                line-height:0.32rem;
                border:0;
                p{
                	font-size:0.28rem;
     				color:rgb(7,17,27);
      				line-height:0.28rem;
      				padding-bottom: 0.24rem;
                }
                .btn1{
                	color:white;
                    background:rgb(0,160,220);
                    &.choosed{
                        background:rgb(0, 160, 220);
                    }
                }
                .btn2{
                	color:rgb(77,85,93);
                    background:rgba(0,160,220,0.2);
                    &.ok{
                        background: rgba(0, 160, 220,0.5);
                    }
                }
                .btn3{
                	color:rgb(77,85,93);
                    background:rgba(77,85,93,0.2);
                     &.no{
                        background-color:rgba(77, 85, 93,0.5);
                        color:white;
                    }
                }
			}
			.check{
				border-top: 1px solid rgba(7,17,27,0.1);
				padding:0.28rem 0;
				i{
	                font-size:0.36rem;
	                color:rgb(147,153,159);
            	}
                .checked {
                    color: rgb(0, 160, 220);
                }
            	span{
                	color:rgb(147,153,159);
                    font-size:0.28rem;
                    line-height:0.36rem;
                    margin-left:0.24rem;
           	 	}
			}			
		}
		.line1{
			background-color: rgba(7,17,27,0.1);
			height: 0.02rem;
		}
		.comment{
			.menu-wrapper{
                .menu-list{
                	.rat{
                		margin: 0.32rem;
                        border-bottom:1px solid rgba(7, 17, 27, 0.1);
                        .userinfo{
                        	display: flex;
                        	justify-content:space-between;
                        	padding-bottom: 0.12rem;
                        	img{
								border-radius: 50%;
                           	 	width: 0.24rem;
                            	height: 0.24rem;
                            	margin-left: 0.12rem;
                        	}
                        }
                        .satisfy{
                            display: flex;
                            font-size: 0.24rem;
                            line-height: 0.48rem;
							padding-bottom: 0.32rem;
                            .good{
                                color: rgb(0, 160, 220); 
                                padding-right: 0.08rem;                                 
                            }
                            .bad {
                                color: rgb(147, 153, 159);
                                padding-right: 0.08rem;                                 
                            }
                            .text {
	                            // line-height: 0.32rem;
	                            color: rgb(7, 17, 27);
                         	}
                        }
              		}
                }
			}
		}
		}
	}
</style>


