<template>
	<div class="goods">
		<!-- 左边 -->
		<div class="menu-wrapper" ref = "menuWrapper">
			<ul>
				<li @click='selectMenu(k,$event)'v-for="(v,k) in goods" :key='k' class="menu-list" :class = '{"current" : currentIndex === k}'>
					<span class="name">{{v.name}}</span>
				</li>
			</ul>
		</div>
		<!-- 右边 -->
		<div class="foods-wrapper"ref = "foodsWrapper">
			<ul>
				<li v-for="(v,k) in goods" :key='k' class="food-list food-list-hook">
					<p class="itemName">{{v.name}}</p>
					<ul>
						<li v-for="(i,k) in v.foods"class="food" @click = 'showfood(i,$event)'>
							<img :src="i.icon" class="foodicon">
							<div class="foodinfo">
								<p class="foodinfoname">{{i.name}}</p>
								<p class="foodinfocount">月售{{i.sellCount}}份&nbsp;好评率{{i.rating}}%</p>
								<div class="foodinfoprice">
									<span class="newPrice">￥{{i.price}}</span><span class="oldPrice"v-if="i.oldPrice>0">￥{{i.oldPrice}}</span>
									<div class="count">
										<count :food='i'></count> 
									</div>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>

		<shopCart></shopCart>
		<foodDetail></foodDetail>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import foodDetail from '../detail/foodDetail.vue'
	import shopCart from '../shopCart/shopCart.vue'
	import count from '../goods/count.vue'

	export default {
		data(){
			return {
				listHeight : [],
				scrollY : 0
			}
		},
		components:{
	     	foodDetail,
	     	shopCart,
	     	count
        },
		created(){
			this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._initFood();
					this._calcHeight();
				},100)
			})
		},
       computed:{
            ...mapGetters([
				'goods',
		    ]),
		    currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    },

		},
		methods : {
			showfood(f,e){
				if( !e._constructed ){
					return;
				};

				this.$store.dispatch('showfood',f);
			},
			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodBS.scrollToElement(el,300);

			},
            _calcHeight(){
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            	console.log(this.listHeight);
            },
			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			},
			_initFood(){
				this.foodBS = new BScroll(this.$refs.foodsWrapper,{
					probeType : 3,
					click : true
				});
				this.foodBS.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
				})
			},
		},
	}
</script>
<style lang="less" scoped>
	.goods{
		position: absolute;
		top:3.48rem;
		bottom: 1.1rem;
		width: 100%;
		font-size: 0.24rem;
		display: flex;
		// 左边
		.menu-wrapper{
			flex:0 0 1.6rem;
			width: 1.6rem;
			background-color: #f3f5f7;
			overflow: hidden;
			.menu-list{
				height: 1.08rem;				
				padding:0 0.24rem;
				display: table;	
				.name{
					text-align: center;
					vertical-align: middle;
					display: table-cell;
					width: 1.12rem;
					border-bottom:0.01rem solid rgba(7, 17, 27, 0.1);
				}			
			}
		}
		// 右边
		.foods-wrapper{
			overflow: hidden;
			flex:1;
			width:5.9rem;
			.itemName{
				font-size: 0.24rem;
				color:rgb(147,153,159);
				padding-left:0.28rem;
				height: 0.52rem;
				line-height: 0.52rem;
				background-color: #f3f5f7;
				border-left:0.03rem solid #d9dde1;
			}
			.food{
				display:flex;
				border-bottom: 0.02rem solid rgba(7,17,27,0.1);
				margin: 0.36rem;
				padding-bottom: 0.36rem;
				.foodicon{
					width:1.1rem;
					height: 1.1rem;
				};
				.foodinfo{
					margin-left: 0.2rem;
					.foodinfoname{
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.04rem;
					};
					.foodinfocount{
						font-size: 0.20rem;
						line-height: 0.20rem;
						padding-top: 0.16rem;
						color:rgb(147,153,159);
					};
					.foodinfoprice{
						width:3.5rem;
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.16rem;
						color:red;
						display: flex;
						// justify-content:space-between;
                        .newPrice{
                        	font-size: 0.28rem;
                        	padding-right: 0.16rem;
                        	color:red;
                        	line-height: 0.48rem;
                        	font-weight: 700;
                        };
                        .oldPrice{
							font-size: 0.2rem;
							line-height: 0.48rem;
							color:rgb(147,153,159);
							text-decoration: line-through;
						}
						.count{
							position: absolute;
							right:0.36rem;
						}

					}
				}
			}
		}
	}
</style>


